* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  background-color: black;
  color: white;
  font-family: sans-serif;
  position: relative;
  text-align: center;
}

.header {
  align-items: center;
  background-color: #333;
  display: flex;
  justify-content: center;
  list-style: none;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
}

.home, .about, .project, .contact, .page-not-found {
  padding-top: 85px;
  position: relative;
}

.footer {
  overflow: hidden;
  background-color: #333;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.menu {
  align-items: center;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  list-style: none;
  padding: 15px 25px 15px 15px;
}

.menu a {
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 15px 15px 5px;
  text-decoration: none;
  font-size: 17px;
}

.nave {
  margin: 0 auto;
}

.title {
  display: none;
  padding: 0 10px;
}

@media screen and (min-width: 660px) {
  .header {
    align-items: center;
    justify-content: space-between;
  }

  .menu a {
    padding: 15px 10px 15px 15px;
  }

  .title {
    display: initial;
    padding: 15px 0 15px 40px;
  }
}